import axios from "axios";

let server;
if (process.env.NODE_ENV === "development") {
  server = "http://127.0.0.10/api";
} else {
}
server = "https://admin.pisi.me/api";

export const authenticateUser = token => {
  return axios({
    method: "post",
    url: `${server}/auth/authenticate`,
    headers: {
      Authorization: token
    }
  }).then(res => res.data);
};
